import React from 'react'
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { Table, TableHead, TableRow, TableCell, TableBody, Paper, makeStyles, Chip } from '@material-ui/core';
import getHeroImage from '../../functions/getHeroImage'

interface Props{
    recent: [{
        assists: any,
        deaths: any,
        duration: any,
        game_mode: any,
        gold_per_min: any,
        hero_damage: any,
        hero_healing: any,
        hero_id: any,
        is_roaming: any,
        kills: any,
        lane: any,
        lane_role: any,
        last_hits: any,
        leaver_status: any,
        lobby_type: any,
        match_id: any,
        party_size: any,
        player_slot: any,
        radiant_win: any,
        start_time: any,
        tower_damage: any,
        version: any,
        xp_per_min: any
    }],
}

const useStyles = makeStyles(theme => ({
    paper: {
      backgroundColor: '#222629',
      marginTop: theme.spacing(3),
      overflowX: 'auto',
    },
    table: {
      minWidth: 650,
    },
    head: {
        color: '#94b4c3',
    },
    body: {
        color: '#eee2dc'
    },
    tablecell: {
        borderBottomColor:  '#3f51b5',
    },
    chip: {
        color: '#5cdb95',
        borderColor: '#5cdb95',
        margin: '20px auto 0',
        display: 'flex',
        width: 200,
    },
  }));

const Recent: React.FC<Props & RouteComponentProps> = ({recent, history}) => {
    const classes = useStyles();
    getHeroImage(1);
    return (
        <>
            <Chip label="Recent Matches" className={classes.chip} variant="outlined"/>
            <Paper className={classes.paper}>    
                <Table className={classes.table}>
                <TableHead>
                <TableRow>
                    <TableCell classes={{head: classes.head, root: classes.tablecell}}>Hero</TableCell>
                    <TableCell classes={{head: classes.head, root: classes.tablecell}} align="center">K/D/A</TableCell>
                    <TableCell classes={{head: classes.head, root: classes.tablecell}} align="center">Duration</TableCell>
                    <TableCell classes={{head: classes.head, root: classes.tablecell}} align="center">GPM</TableCell>
                    <TableCell classes={{head: classes.head, root: classes.tablecell}} align="center">XPM</TableCell>
                </TableRow>
                </TableHead>
                <TableBody>
                {recent.map(match => (
                    <TableRow key={match.match_id} hover onClick={() => history.push(`/match/${match.match_id}`)}>
                        <TableCell classes={{body: classes.body, root: classes.tablecell}}>{match.hero_id}</TableCell>
                        <TableCell classes={{body: classes.body, root: classes.tablecell}} align="center">{match.kills}/{match.deaths}/{match.assists}</TableCell>
                        <TableCell classes={{body: classes.body, root: classes.tablecell}} align="center">{Math.round(match.duration/60)}</TableCell>
                        <TableCell classes={{body: classes.body, root: classes.tablecell}} align="center">{match.gold_per_min}</TableCell>
                        <TableCell classes={{body: classes.body, root: classes.tablecell}} align="center">{match.xp_per_min}</TableCell>
                    </TableRow>
                ))}
                </TableBody>
            </Table>
        </Paper>
      </>  
    )
}

export default withRouter(Recent)
